.btn {
  border: none;
  background-color: #ff650e;
  border-radius: 40px;
  align-items: center;
  color: #fff;
  cursor: pointer;
  display: inline-flex;
  font-family: 'Gordita', 'Arial', 'Helvetica', sans-serif;
  font-size: 1.6rem;
  justify-content: center;
  line-height: 1;
  max-height: 6.4rem;
  overflow: hidden;
  padding: 2.4rem 4rem;
  position: relative;
  vertical-align: bottom;
}
.btn:before {
  background-color: #fa5a00;
  content: '';
  left: 0;
  height: 100%;
  position: absolute;
  top: 0;
  transition: all 0.4s cubic-bezier(0.4, 0, 0, 1);
  width: 0;
  z-index: 0;
}
.btn__text {
  position: relative;
  transition: all 0.4s cubic-bezier(0.4, 0, 0, 1);
  z-index: 10;
}
.btn__text:nth-child(2) {
  opacity: 0;
  position: absolute;
  transform: translateY(100%);
}
.btn_ghost {
  background-color: transparent;
}
.btn_ghost:before {
  display: none;
}
.btn_ghost .btn__text {
  color: #ff650e;
}
.btn:hover:before {
  width: 100%;
}
.btn:hover .btn__text:nth-child(1) {
  opacity: 0;
  transform: translateY(-100%);
}
.btn:hover .btn__text:nth-child(2) {
  opacity: 1;
  transform: none;
}
.btn--small {
  padding: 1.6rem 2.4rem;
}
.btn--transparent {
  background-color: rgba(255,255,255,0.2);
}
.btn--full {
  width: 100%;
}
.btn + .btn {
  margin-left: 2rem;
}
.btn + .slider {
  margin-top: 3.2rem;
}
.form-control {
  margin-bottom: 2rem;
  position: relative;
  width: 100%;
}
.form-control label,
.form-control__label {
  color: #151514;
  cursor: text;
  display: inline-block;
  line-height: 1;
  font-weight: 400;
  margin-bottom: 0.75em;
  transition: all 0.2s;
  max-width: 100%;
}
.form-control input[type="text"],
.form-control input[type="password"],
.form-control input[type="date"],
.form-control input[type="email"],
.form-control input[type="tel"],
.form-control input[type="number"],
.form-control input[type="search"],
.form-control select,
.form-control textarea,
.form-control .custom-select {
  background: #fff;
  border: none;
  border: 1px solid #dee6eb;
  border-radius: 4px;
  color: #151514;
  display: block;
  font-family: 'Gordita', 'Arial', 'Helvetica', sans-serif;
  font-size: 1.6rem;
  line-height: 1;
  min-height: 5rem;
  transition: border-color 0.2s;
  padding: 2rem 2.4rem;
  position: relative;
  transition: all 0.4s cubic-bezier(0.4, 0, 0, 1);
  resize: vertical;
  width: 100%;
  -webkit-appearance: none;
}
.form-control input[type="text"]:hover,
.form-control input[type="password"]:hover,
.form-control input[type="date"]:hover,
.form-control input[type="email"]:hover,
.form-control input[type="tel"]:hover,
