.checkbox {
  align-items: center;
  cursor: pointer;
  display: flex;
  margin-bottom: 1em;
  user-select: none;
}
.checkbox__label {
  background: transparent;
  border-radius: 4px;
  border: 1px solid #dee6eb;
  cursor: pointer;
  height: 1.8rem;
  width: 1.8rem;
  display: inline-block;
  flex-shrink: 0;
  margin-right: 0.4rem;
  position: relative;
  transform: translateY(-1px);
  z-index: 1;
}
.checkbox__label:after {
  color: #151514;
  content: url("../../img/check.svg");
  display: none;
  font-size: 1.3rem;
  left: 2px;
  position: absolute;
  top: 2px;
  z-index: 10;
}
.checkbox__text {
  display: inline-block;
}
.checkbox .link {
  display: inline-block;
  margin-bottom: 0;
}
.checkbox input {
  display: none;
}
.checkbox input:checked + .checkbox__label:after {
  display: block;
}
.radio {
  align-items: center;
  cursor: pointer;
  display: flex;
  margin-bottom: 1rem;
  user-select: none;
}
.radio__label {
  background: #fff;
  border-radius: 50%;
  border: 1px solid #dee6eb;
  cursor: pointer;
  height: 3.2rem;
  width: 3.2rem;
  display: inline-block;
  margin-right: 1rem;
  position: relative;
  z-index: 1;
}
.radio__label:after {
  align-items: center;
  content: url("../../img/check.svg");
  display: none;
  justify-content: center;
  margin-top: -2px;
  height: 100%;
  width: 100%;
  position: absolute;
  z-index: 10;
}
.radio input {
  display: none;
}
.radio input:checked + .radio__label {
  border-color: #ff650e;
}
.radio input:checked + .radio__label:after {
  display: flex;
}
